.editor {
    display: flex;
    flex-direction: row;
    height: 100%;

    &_preview {
        height: 100%;
        flex-basis: 50%;
        overflow: auto;
        text-align: center;
    }

    &_error {
        padding: 1rem;
    }

    &_scenario {
        position: relative;
        flex-grow: 1;
        height: 100%;
    }

    &_textareawrapper {
        position: absolute;
        top: 1rem;
        left: 1rem;
        right: 1rem;
        bottom: 1rem;

        & textarea {
            box-sizing: border-box;
            font-size: 20px;
            padding: 1rem;
            border: none;
            height: 100%;
            width: 100%;
            outline: 4px solid transparent;
            outline-offset: 2px;

            &:focus {
                outline-color: rgba(42, 141, 255, 0.5);
            }
        }
    }
}
