.landing {
    &-title {
        text-align: center;
        color: white;
        font-family: serif;
    }

    &-templates {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
    }

    &-template {
        margin: 1rem;
        display: flex;
        flex-direction: column;
        text-align: center;
        cursor: pointer;
        padding: 1rem;
        background-color: white;

        &:hover {
            box-shadow: 0 0 5px 4px #00000066;
            outline: 3px solid #008cff;
            outline-offset: -4px;
        }

        &-title {
            display: block;
        }

        &-preview {
            display: inline-block;
        }
    }
}