.app {
    height: 100%;

    &_header {
        background-color: #fefefe;
        position: absolute;
        top:0;
        left:0;
        right: 0;
        height: 36px;
        line-height: 36px;
        vertical-align: middle;
        font-size: 24px;
        padding: 0 1rem;
        display: flex;
        box-shadow: 0 0 2px #00000066;
        z-index: 1;

        & .app_logo {
            display: inline-block;
            text-align: left;
        }

        & .app_spring {
            flex-grow: 1;
        }

        & .app_follow {
            display: inline-block;
            padding: 0 1rem;
            font-size: 18px;
            text-align: right;

            &>a {
                color: rgb(0, 140, 255);
                text-decoration: none;

                &:link, &:visited {
                    color: rgb(0, 140, 255);
                }
            }
        }
    }

    &_body {
        background-color: #333;
        padding-top: 36px;
        height: 100%;
        position: relative;
        box-sizing: border-box;
        overflow: auto;
    }
}


@media (max-width: 640px) {

    .app {

        // setting subcomponent styles here
        // todo: refactor
        .editor {
            flex-direction: column-reverse;

            &_preview {
                overflow: unset;
                flex-grow: 1;
                flex-basis: auto;
                height: auto;
            }

            &_textareawrapper {
                top: 0.25rem;
                left: 0.25rem;
                right: 0.25rem;
                bottom: 0.25rem;
            }
        }

        .preview {
            padding: 0.25rem;
        }

        .frames-preview {
            display: none;
        }
    }

}